.coast {
	@include coast();
	height: 37px;

	@media ($tablet) {
		display: inline-flex;
		flex-direction: column;
		border-radius: 15px;
		margin-right: 10px;
		height: auto;
	}
	// .coast__top
	&__top {
		@include h3($align: left);
		font-size: 14px;
		font-family: $mainFont;
		line-height: 37px;
		background-color: $mainColor;
		flex-basis: 50%;
		padding-left: 20px;

		@media ($tablet) {
			padding-left: 0;
			text-align: center;
			padding: 10px 20px;
			line-height: 21px;
		}
	}
	// .coast__bottom
	&__bottom {
		@include h4($align: left);
		font-weight: bold;
		font-family: $mainFont;
		background-color: #e9f1f3;
		width: 100%;
		flex-basis: 50%;
		padding-left: 20px;
		

		@media ($tablet) {
			padding-left: 0;
			text-align: center;
			font-weight: bold;
			padding: 20px 0;
			line-height: 21.6px;
		}
	}

	&:last-child {
		@media ($tablet) {
			margin-right: 0;
		}
	}
}
