.advantages {
	@media ($desktop) {
		display: flex;
		justify-content: stretch;
		margin-top: 80px;
	}

	// .advantages__content

	// .advantages__image
	&__image {
		display: none;

		@media ($desktop) {
			display: flex;
			justify-content: stretch;
			max-width: 475px;

		}
	}
}
.advantages-content {
	// .advantages-content__caption
	&__caption {
		@include h1();
		margin: 30px 43px 30px 0px;

		@media ($tablet) {
			text-align: center;
		}
		@media ($desktop) {
			text-align: left;
			margin: 30px 85px 60px 0px;
			font-size: 40px;
		}
		@media (min-width: 1280px) {
			font-size: 58px;
			width: 755px;
		}
	}
	// .advantages-content__items
	&__items {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		@media (min-width: 375px) {
			justify-content: space-around;
		}
		
		
		@media ($desktop) {
			justify-content: flex-start;
			
		}
	}
	// .advantages-content__item
	&__item {
		margin: 0 0 16px 0;
		
		@media ($desktop) {
			max-width: 250px;
			
			margin: 0 30px 30px 0;
		}
	}
}

.advantages-btn {
	@media ($tablet) {
		text-align: center;
	}
	@media ($desktop) {
		max-width: 522px;
		min-height: 80px;
	}
}

.btn {
	@extend %btn;
	margin-top: 15px;
	width: 100%;
	height: 57px;

	@media ($desktop) {
		width: 100%;
		height: 80px;
		font-size: 18px;
		margin-top: 30px;
	}
	@media (max-width: 1023.9px) and (min-width: 768px) {
		width: 100%;
		height: 70px;
		font-size: 16px;
	}
}
